*,
*::before,
*::after {
  box-sizing: border-box; }

html {
  box-sizing: border-box; }

body {
  width: 100%;
  height: 100%;
  min-width: 375px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.15;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #2B2D33;
  background-color: #E3E5EA;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

h1, h2 {
  font-family: "Merriweather", "Times New Roman", serif;
  font-weight: 900; }

a {
  color: #000000;
  text-decoration: none; }

img,
video {
  display: block;
  max-width: 100%;
  height: auto; }

textarea {
  resize: none; }

/* :focus {
  outline: none;
} */
input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000; }

input,
textarea {
  border-radius: 0; }
  input::placeholder,
  textarea::placeholder {
    opacity: 1; }
  input:invalid,
  textarea:invalid {
    box-shadow: none; }

select {
  border-radius: 0; }

input::-ms-clear, input::-ms-reveal {
  display: none; }

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  appearance: none; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none; }

input[type="number"] {
  appearance: textfield; }

select,
textarea,
input:matches([type="email"],
[type="number"],
[type="password"],
[type="search"],
[type="tel"],
[type="text"],
[type="url"]) {
  appearance: none; }

button,
[type="button"],
[type="reset"],
[type="submit"] {
  appearance: none; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0); }

.scroll-lock {
  position: fixed;
  overflow-y: auto; }

.btn-reset {
  padding: 0;
  font: inherit;
  color: currentColor;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer; }

.list-reset {
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;
  list-style-type: none; }

.page {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  overflow-x: hidden; }

.page-header,
.page-footer {
  flex-shrink: 0; }

.page-main {
  flex-grow: 1; }

.container {
  min-width: 375px;
  max-width: 1230px;
  padding-left: 20px;
  padding-right: 20px;
  box-sizing: border-box;
  margin: 0 auto; }

.guitar {
  background-color: brown; }
